.ModalButtonStyle {
  width: 50px;
  height: 50px;
  position: fixed;
  right: 10px;
  bottom: 10px;
  text-align: center;
  border-radius: 100px;
  background-color: #FFD6A4;
  border: none;
  outline: 0; }
  .ModalButtonStyle:hover {
    background-color: #fff;
    border: 2px solid #FDE9DF; }
  .ModalButtonStyle p {
    font-weight: 600;
    font-size: 120%;
    color: #000; }
    .ModalButtonStyle p:hover {
      color: #000; }

.ModalWrapper {
  color: #fff;
  background-color: #282D4F;
  border: none;
  padding: 10px; }

@media screen and (min-width: 768px) {
  .ModalParent {
    width: 60%; } }

.modalPushButton {
  background-color: #FF6C00;
  color: #fff; }
