.modalCloseButton {
  background-color: #A0204C;
  color: #fff; }
  @media screen and (min-width: 768px) {
    .modalCloseButton {
      width: 60px;
      height: 60px;
      text-align: center;
      border: none;
      border-radius: 100%; } }
  @media screen and (max-width: 767px) {
    .modalCloseButton {
      width: 60px;
      height: 30px;
      text-align: center;
      border: none; } }

@media screen and (min-width: 768px) {
  .displayButton {
    display: block; } }

@media screen and (max-width: 767px) {
  .displayButton {
    display: none; } }

.dropdownCustom {
  width: 100%;
  height: 60px;
  border: 1px solid #fff; }



