@media screen and (min-width: 768px) {
  .loginForm {
    padding: 70px;
    padding-left: 500px;
    padding-right: 500px; } }

@media screen and (max-width: 767px) {
  .loginForm {
    padding: 30px; } }

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .loginForm {
    padding-left: 300px;
    padding-right: 300px; } }



